/**
 * @name 
 * @param option 
 * @desc option 
 * @param {Date | string} option.startDate  
 * @param {Date | string} option.endDate 
 * @param {Function} option.success 
 * @param {Function} option.fail 
 * @return {
      day : dayDiff, //
      hours : hours, //
      minutes : minutes, //
      seconds : seconds, //
    }
*/
export default (option) => { //
  let options = {
    startDate: null,
    endDate: null,
    success: (reslut) => { },
    fail: (error) => { },
  }
  options = {
    ...options,
    ...option
  }
  if (!options.startDate) {
    options.fail({
      code: -1,
      msg: "startDate must be null"
    })
    return;
  }
  //
  let dateBegin = "";
  if (typeof options.startDate == 'string') {
    dateBegin = options.startDate.replace(/T/g, " "); //-/new Date
    dateBegin = new Date(dateBegin.replace(/-/g, "/")); //-/new Date 
  } else {
    dateBegin = options.startDate;
  }

  if (options.endDate && typeof options.endDate == 'string') {
    options.endDate = options.endDate.replace(/T/g, " "); //
    options.endDate = new Date(options.endDate.replace(/-/g, '/'));
  }
  let dateEnd = options.endDate || new Date(); //
  let dateDiff = dateBegin.getTime() - dateEnd.getTime(); //
  let dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000)); //
  let leave1 = dateDiff % (24 * 3600 * 1000) //
  let hours = Math.floor(leave1 / (3600 * 1000)) //
  //
  let leave2 = leave1 % (3600 * 1000) //
  let minutes = Math.floor(leave2 / (60 * 1000)) //
  //
  let leave3 = leave2 % (60 * 1000) //
  let seconds = Math.round(leave3 / 1000);
  options.success({
    day: dayDiff, //
    hours: hours, //
    minutes: minutes, //
    seconds: seconds, //
  })
}