/**
 * html 
 * @description  100% 
 * @param {object} option 
 * @description option 
 * @param {string} option.html html
 * @param {string} [option.imgUrl] url 
 * @return {Function} option.success 
 * @return {Function} option.fail 
 * */ 

const setHtml = (option)=>{
    let options = {
        html : "",
        imgUrl : "",
        success : (res)=>{},
        fail : (error)=>{}
    }
    options = {
        ...options,
        ...option
    }
    if(!options.html){
        options.fail("html is null")
        return ;
    }
    if(typeof options.html != 'string'){
        options.fail("html is not string")
        return ;
    }
    let html = options.html.replaceAll("  &lt;", "<").replaceAll("  &gt;", ">");

    let newContent = html.replace(/<img[^>]*>/gi, function(match, capture) {
		if (match.search(/style=/gi) == -1) {
			match = match.replace(/\<img/gi, '<img style=""');
		}
		return match;
	});
    newContent = newContent.replace(/style="/gi, '$& max-width:100% ; height:auto; ');
	var str = /<img [^>]*src=['"]([^'"]+)[^>]*>/g; // img src
	if (!newContent.match(str)) {
        options.success(newContent)
		return;
	}
    console.log("newContent ==>",newContent)
    var allImg = newContent.match(str); // img  s
	// console.log(allImg);
	for (var i = 0; i < allImg.length; i++) {
		var srcImg = allImg[i].replace(str, '$1'); //src
        // http https base64 
		if (srcImg.slice(0, 4) === 'http' || srcImg.slice(0, 5) === 'https' || srcImg.slice(0, 4) === 'data') {
			continue;
		} else {
			newContent = newContent.replace(new RegExp(srcImg, 'g'), options.imgUrl + srcImg);
		}
	}
    options.success(newContent)
}

export default setHtml