//====  offset , 0=
function cyclical(num) {
    return Gan[num % 10] + Zhi[num % 12];
}

//====  year
function lYearDays(year) {
    let i,
        sum = 348;
    for (i = 0x8000; i > 0x8; i >>= 1) {
        sum += lunarInfo[year - 1900] & i ? 1 : 0;
    }
    return sum + leapDays(year);
}

//====  year
function leapDays(year) {
    if (leapMonth(year)) {
        return lunarInfo[year - 1900] & 0x10000 ? 30 : 29;
    } else {
        return 0;
    }
}

//====  year 1-12 ,  0
function leapMonth(year) {
    return lunarInfo[year - 1900] & 0xf;
}

//====  yearmonth
function monthDays(year, month) {
    return lunarInfo[year - 1900] & (0x10000 >> month) ? 30 : 29;
}

//==== , , 
//      year month day isLeap yearCyl dayCyl monCyl
function Lunar(objDate) {
    let i,
        temp = 0;
    let baseDate = new Date(1900, 0, 31);
    let offset = Math.floor((objDate - baseDate) / 86400000);

    let dayCyl = offset + 40;
    let monCyl = 14;

    for (i = 1900; i < 2050 && offset > 0; i++) {
        temp = lYearDays(i);
        offset -= temp;
        monCyl += 12;
    }
    if (offset < 0) {
        offset += temp;
        i--;
        monCyl -= 12;
    }
    //
    let year = i;
    let yearCyl = i - 1864;

    let leap = leapMonth(i); //
    let isLeap = false; //

    for (i = 1; i < 13 && offset > 0; i++) {
        //
        if (leap > 0 && i === leap + 1 && isLeap === false) {
            --i;
            isLeap = true;
            temp = leapDays(year);
        } else {
            temp = monthDays(year, i);
        }

        //
        if (isLeap === true && i === leap + 1) {
            isLeap = false;
        }

        offset -= temp;
        if (isLeap === false) {
            monCyl++;
        }
    }

    if (offset === 0 && leap > 0 && i === leap + 1)
        if (isLeap) {
            isLeap = false;
        } else {
            isLeap = true;
            --i;
            --monCyl;
        }

    if (offset < 0) {
        offset += temp;
        --i;
        --monCyl;
    }
    //
    let month = i;
    //
    let day = offset + 1;

    return {
        year: year,
        month: month,
        day: day,
        isLeap: isLeap,
        leap: leap,
        yearCyl: yearCyl,
        dayCyl: dayCyl,
        monCyl: monCyl,
    };
}

//====  md
function cDay(m, d) {
    let nStr1 = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
    ];
    let nStr2 = ["", "", "", "", ""];
    //
    let lunarMonthCn;
    //
    let lunarDayCn;
    if (m > 10) {
        lunarMonthCn = "" + nStr1[m - 10];
        if (nStr1[m - 10] == "") {
            lunarMonthCn = "";
        }
    } else {
        lunarMonthCn = nStr1[m];
        if (nStr1[m] == "") {
            lunarMonthCn = "";
        }
    }
    lunarMonthCn += "";

    switch (d) {
        case 10:
            lunarDayCn = "";
            break;
        case 20:
            lunarDayCn = "";
            break;
        case 30:
            lunarDayCn = "";
            break;
        default:
            lunarDayCn = nStr2[Math.floor(d / 10)] + nStr1[d % 10];
    }
    return {
        lunarMonthCn: lunarMonthCn,
        lunarDayCn: lunarDayCn,
    };
}

