//    
//  
class CreatedTree{
    constructor(data , left , right){
        this.data = data;
        this.left = left;
        this.right = right;
        this.count = 1;
    }
}

/**
 * 
 * @method insert 
 * @method romve 
 * @method find 
 * @method getMaxNode 
 * @method getMinNode 
 * 
*/ 
class BSTree{
    constructor(TreeNode = null){
        /**
         * 
         * */ 
        this.root = TreeNode || null;
    }

    // 
    #_romoveNode(node , data){
        if(!node){
            return null
        }
        if(data == node.data){
            //  null
            if(!node.left && !node.right){
                return null
            }
            //  
            if(!node.left) return node.right;
            //  
            if(!node.right) return node.left;
            //  
            /*  
             
                 
                
            */
 
            // 
            let temNode = this.getMinNode(node.right);
            node.data = temNode.data;
            node.right = this.#_romoveNode(node.right , temNode.data);
            return node;
        }else if (data < node.data){
            node.left = this.#_romoveNode(node.left , data);
            return node;
        }else{
            node.right = this.#_romoveNode(node.right , data);
            return node;
        }
    }

    /**
     * 
     * @param {string} data 
     * */ 
    romve(data){
        this.root = this.#_romoveNode(this.root , data)
    }

    /**
     * 
     * @param {string} data 
     * */ 
    insert(data){
        let newNode = new CreatedTree(data , null , null)
        if(!this.root){
            this.root = newNode;
            return ;
        }
        let currNode = this.root;
        let parentNode = null;
        while(true){
            parentNode = currNode;
            if(newNode.data < currNode.data){
                // 
                currNode = currNode.left;
                // 
                if(!currNode){
                    parentNode.left = newNode;
                    break;
                }
            }else if(newNode.data > currNode.data){
                // 
                currNode = currNode.right;
                // 
                if(!currNode){
                    parentNode.right = newNode;
                    break;
                }
            }else if(newNode.data == currNode.data){
                // 
                currNode.count ++;
                break;
            }
        }
    }

    /**
     * 
     * @param {string} data 
     * @return findNode
     */
    find(data){
        let currNode = this.root;
        while(currNode){
            if(currNode.data == data){
                return currNode
            }else if(currNode.data < data){
                currNode = currNode.right;
            }else{
                currNode = currNode.left;
            }
        }
        return null
    }

    /**
     * 
     * @return minNode
     * */ 
    getMinNode(node = this.root){
        if(!node)return null;
        let currNode = node;
        while(currNode.left){
            currNode = currNode.left;
        }
        return currNode;
    }

    /**
     * 
     * @return maxNode
     */
    getMaxNode(node = this.root){
        if(!node)return null;
        let currNode = node;
        while(currNode.right){
            currNode = currNode.right;
        }
        return currNode;
    }
}

/**
 * 
 * @method insert 
 * @method romve 
 * @method find 
 * @method getMaxNode 
 * @method getMinNode 
 * @retrun treeNode
 * @description    
 * @description   
*/ 
export const CreatedTreeNode = new BSTree;


/**
 * 
 * @param {object} option 
 * @param {object} [option.TreeNode] 
 * @param {Function} option.success 
 * @param {Function} [option.fail] 
 * @retrun success return  treeNode
 * @description    
 * @description   
*/ 
export const CreatedTreeNodes = (option) =>{
    let options = {
        TreeNode : null , 
        success : (reslut)=>{},
        fail : (error) =>{}
    }
    options = {
        ...options,
        ...option
    }
    options.success(new BSTree(options.TreeNode));
}

export default {
    CreatedTreeNode,
    CreatedTreeNodes
}