 * @FilePath: \vue2+elui+template\src\components\dzs-markdown\index.vue
-->
<template>
    <div class="markdown-box" :style="{height:height,}">
        <mavon-editor :toolbars="markdownOption" v-model="content" @change="changeVaule" @sava="sava" @imgAdd="$imgAdd"></mavon-editor>
    </div>
</template>
<script>
/*
 * @name markdown 
 * @prop value string    v-model / update:value
 * @prop height String default 500px 
 * @prop width string default 100% 
 * @prop markdownOption  https://github.com/hinesboy/mavonEditor
 */
import "mavon-editor/dist/css/index.css";
export default {
    name: "dzs-markdown",
    model: {
        prop: "value",
        event: "update:value",
    },
    props: {
        value: {
            type: String,
            default: () => {
                return "";
            },
        },
        height: {
            type: String,
            default: () => {
                return "500px";
            },
        },
        width: {
            type: String,
            default: () => {
                return "100%";
            },
        },
        markdownOption: {
            type: Object,
            default: () => {
                return {
                    bold: true, // 
                    italic: true, // 
                    header: true, // 
                    underline: true, // 
                    strikethrough: true, // 
                    mark: true, // 
                    superscript: true, // 
                    subscript: true, // 
                    quote: true, // 
                    ol: true, // 
                    ul: true, // 
                    link: true, // 
                    imagelink: true, // 
                    code: true, // code
                    table: true, // 
                    fullscreen: true, // 
                    readmodel: true, // 
                    htmlcode: true, // html
                    help: true, // 
                    /* 1.3.5 */
                    undo: true, // 
                    redo: true, // 
                    trash: true, // 
                    save: true, // eventssave
                    /* 1.4.2 */
                    navigation: true, // 
                    /* 2.1.8 */
                    alignleft: true, // 
                    aligncenter: true, // 
                    alignright: true, // 
                    /* 2.2.1 */
                    subfield: true, // 
                    preview: true, // 
                };
            },
        },
    },
    watch: {
        value: {
            handler(newValue, oldValue) {
                this.content = newValue;
            },
            deep:true,
            immediate:true
        },
    },
    data() {
        return {
            content: "",
        };
    },
    mounted() {},
    methods: {
        changeVaule(e) {
            this.$emit("update:value", this.content);
            this.$emit("change", e);
        },
        sava() {
            this.$emit("update:value", this.content);
            this.$emit("sava", e);
        },
        $imgAdd(pos, $file) {
            let formdata = new FormData();
            formdata.append("image", $file);
        },
    },
};
</script>
<style lang="less" scoped>
.markdown-box {
    :deep(.v-note-wrapper) {
        height: 100%;
        z-index: 10;
    }
}
</style>