
<template>
    <div>
        <dzs-header title="html" v-if="!newIndex"></dzs-header>
        <div class="page">
            <div class="tips-box"> </div>
            <h2></h2>
            <div class="item">
                <div class="tips-box"></div>
                <div v-html="oldHtml"></div>
                <div class="tips-box"> 100%</div>
                <div v-html="newHtml"></div>
                <dzs-code title="" :value="code1"></dzs-code>
            </div>
            <h2>/</h2>
            <div class="item">
                <a target="_blank" href="https://github.com/zsduan/zsduan-summary/blob/master/src/utils/sethtml.js">/</a>
            </div>
        </div>
        <el-backtop :visibility-height="20" v-if="!newIndex">
        </el-backtop>
    </div>
</template>
<script>
import setHtml from "../../utils/sethtml"
import {code1} from "./config"
import imgUrl from "./image/1.jpg"
export default {
    props:{
        newIndex : {
            type : Boolean,
            default:()=>{
                return false
            }
        }
    },
    data(){
        return {
            oldHtml : `<img src='${imgUrl}' />`,
            code1 : code1,
            newHtml : ""
        }
    },
    mounted(){
        this.mysetHtml();
    },
    methods:{
        mysetHtml(){
            setHtml({
                html : this.oldHtml,
                success : (res)=>{
                    this.newHtml = res;
                },
            })
        }
        
    }
}
</script>
<style lang="less" scoped>
</style>