<template>
    <div>
        <dzs-header title="Notification " v-if="!newIndex"></dzs-header>
        <div class="page">
            <h2> </h2>
            <div class="tips-box">https</div>
            <div class="item">
                <div class="my-box">
                    <el-input class="ipt" v-model="Text"  placeholder=""></el-input>
                    <el-button type="primary" class="btn" @click="sendMsg"></el-button>
                </div>
                <dzs-code title="" :value="code1"></dzs-code>
            </div>
            <h2> </h2>
            <div class="item">
                <div class="my-box">
                    <el-input class="ipt" v-model="Text2"  placeholder=""></el-input>
                    <el-input class="ipt" v-model="Text3"  placeholder=""></el-input>
                    <el-button type="primary" class="btn" @click="sendMsg2"></el-button>
                </div>
                <dzs-code title="" :value="code2"></dzs-code>
            </div>
            <h2> </h2>
            <div class="item">
                <div class="my-box">
                    <el-input class="ipt" v-model="Text4"  placeholder=""></el-input>
                    <el-input class="ipt" v-model="Text5" type="Number" placeholder=""></el-input>
                    <el-button type="primary" class="btn" @click="sendMsg3"></el-button>
                </div>
                <dzs-code title="" :value="code3"></dzs-code>
            </div>
            <h2> </h2>
            <div class="item">
                <div class="my-box">
                    <el-input class="ipt" v-model="Text6"  placeholder=""></el-input>
                    <el-input class="ipt" v-model="Text7"  placeholder=""></el-input>
                    <el-button type="primary" class="btn" @click="sendMsg4"></el-button>
                </div>
                <dzs-code title="" :value="code4"></dzs-code>
            </div>
            <h2>/</h2>
            <div class="item">
                <a target="_blank" href="https://github.com/zsduan/zsduan-summary/blob/master/src/utils/notification.js">/</a>
            </div>
        </div>
        <el-backtop :visibility-height="20" v-if="!newIndex"></el-backtop>
    </div>
</template>
<script>
import {notification} from "@/utils/notification.js";
import audioUrl from "../../assets/audio/newMsg.mp3";
import {code1 , code2 , code3 , code4 } from "./config"
export default {
    props:{
        newIndex : {
            type : Boolean,
            default:()=>{
                return false
            }
        }
    },
    data(){
        return {
            Text : "",
            Text2 : "",
            Text3 : "",
            Text4 : "",
            Text5 : "3000",
            Text6 : "",
            Text7 : audioUrl,
            code1 : code1,
            code2 : code2,
            code3 : code3,
            code4 : code4,
        }
    },
    mounted(){

    },
    methods:{
        sendMsg(){
            if(!this.Text){
                this.errorTips();
                return
            }
            notification(this,{
                title : this.Text,
            })
        },
        sendMsg2(){
            if(!this.Text2){
                this.errorTips();
                return
            }
            notification(this,{
                title : this.Text2,
                msg : this.Text3
            })
        },
        sendMsg3(){
            if(!this.Text4){
                this.errorTips();
                return
            }
            notification(this,{
                title : this.Text4,
                msg : "" + this.Text5,
                duration : this.Text5 ? Number(this.Text5) : 5000
            })
        },
        sendMsg4(){
            if(!this.Text6){
                this.errorTips();
                return
            }
            notification(this,{
                title : this.Text6,
                msg : "" + this.Text7,
                duration : 5000,
                audioUrl: this.Text7
            })
        },
        errorTips(){
            this.$message({
                message : "",
                type : "error"
            })
        }
    }
}
</script>
<style lang="less" scoped>
.my-box{
    display: flex;
    .ipt{
        width: 30%;
        margin-left: 10px;
        &:first-child{
            margin-left: 0;
        }
    }
    .btn{
        display: inline-block;
        margin-left: 10px;
    }
}
</style>