export const code1 = `

// 
import {notification} from "@/utils/notification.js";
// 
<div class="item">
    <div class="my-box">
        <el-input class="ipt" v-model="Text"  placeholder=""></el-input>
        <el-button type="primary" class="btn" @click="sendMsg"></el-button>
    </div>
</div>
// data
Text
// methods
/*
 * @name 
 * @parame _this this
 * @parame title  string 
 * @parame msg  string
 * @parame audioUrl  string
 * @parame duration  number
 * @method success  code 100 200
 * @method fail 
 *
*/ 
sendMsg(){
    notification(this,{
        title : Text
    })
},
`;

export const code2 = `
// 
import {notification} from "@/utils/notification.js";
// 
<div class="item">
    <div class="my-box">
        <el-input class="ipt" v-model="Text"  placeholder=""></el-input>
        <el-input class="ipt" v-model="Text1"  placeholder=""></el-input>
        <el-button type="primary" class="btn" @click="sendMsg"></el-button>
    </div>
</div>
// methods
/*
 * @name 
 * @parame _this this
 * @parame title  string 
 * @parame msg  string
 * @parame audioUrl  string
 * @parame duration  number
 * @method success  code 100 200
 * @method fail 
 *
*/ 
sendMsg(){
    if(!this.Text){
        this.errorTips();
        return
    }
    notification(this,{
        title : Text,
        msg : Text
    })
},
`;

