const PENDING = 'pending';
const RESOVLED = 'resolved';
const REJECTED = 'rejected';

class myPromise {
    constructor(executor) {
        let that = this;
        this.status = PENDING;   //pending
        this.data = undefined;     //
        this.callbacks = [];       //onResolved(){},onRejected(){}
        //
        //promise
        function resolve(value) {
            if (that.status !== PENDING) return
            //resolveresolved
            that.status = RESOVLED;
            //data
            that.data = value;
            //callbackonResolved,
            if (that.callbacks.length > 0) {
                setTimeout(() => {
                    that.callbacks.forEach(element => {
                        element.onResolved(value)
                    });
                });
            }
        }
        function reject(reason) {
            if (that.status !== PENDING) return
            //rejectrejected
            that.status = REJECTED;
            //data
            that.data = reason;
            //callbackonRejected,
            if (that.callbacks.length > 0) {
                setTimeout(() => {
                    that.callbacks.forEach(element => {
                        element.onResolved(reason)
                    });
                });
            }
        }
        //
        //resolvereject,try catch
        try {
            executor(resolve, reject)
        } catch (error) {
            reject(error);
        }
    }
    /* 
//.then
Promise
*/
    then(onResolved, onRejected) {
        const that = this;
        // ()
        onResolved = typeof onResolved === 'function' ? onResolved : value => value  //
        onRejected = typeof onRejected === 'function' ? onRejected : reason => { throw reason }  //
        return new Promise((resolve, reject) => {
            if (this.status === PENDING) {
                //pending,
                this.callbacks.push({
                    onResolved(value) {
                        onResolved(that.data);
                        try {
                            const result = onResolved(that.data)
                            if (result instanceof Promise) {
                                result.then(resolve, reject
                                    // value =>{
                                    //     resolve(value)
                                    // },
                                    // reason=>{
                                    //     reject(reason)
                                    // }
                                )
                            }
                        } catch (error) {
                            resolve(error);
                        }
                    },
                    onRejected(reason) {
                        onRejected(that.data);
                        try {
                            const result = onRejected(that.data)
                            if (result instanceof Promise) {
                                result.then(resolve, reject
                                    // value =>{
                                    //     resolve(value)
                                    // },
                                    // reason=>{
                                    //     reject(reason)
                                    // }
                                )
                            }
                        } catch (error) {
                            reject(error);
                        }
                    }
                })
            } else if (this.status === RESOVLED) {
                setTimeout(() => {
                    //  promise,returnpromisepromise
                    try {
                        const result = onResolved(that.data)
                        if (result instanceof Promise) {
                            result.then(resolve, reject
                                // value =>{
                                //     resolve(value)
                                // },
                                // reason=>{
                                //     reject(reason)
                                // }
                            )
                        } else {//promisepromisevalue
                            resolve(result)
                        }
                    } catch (error) {
                        reject(error);
                    }
                });
            } else {
                //promise
                setTimeout(() => {
                    //  promise,returnpromisepromise
                    try {
                        const result = onRejected(that.data)
                        if (result instanceof Promise) {
                            result.then(resolve, reject
                                // value =>{
                                //     resolve(value)
                                // },
                                // reason=>{
                                //     reject(reason)
                                // }
                            )
                        }
                    } catch (error) {
                        try {
                            const result = onResolved(that.data)
                            if (result instanceof Promise) {
                                result.then(resolve, reject
                                    // value =>{
                                    //     resolve(value)
                                    // },
                                    // reason=>{
                                    //     reject(reason)
                                    // }
                                )
                            }
                        } catch (error) {
                            reject(error);
                        }
                    }
                });
            }
        })
    }
    /*
    
    promise
     */
    catch(onRejected) {
        return this.then(undefined, onRejected)
    }
    /*
    promiseresolve
     */
    static resolve(value) {
        return new Promise((resolve, reject) => {
            if (value instanceof Promise) {
                value.then(resolve, reject)
            } else {
                resolve(value);
            }
        })
    }
    /*
    promisereject
     */
    static reject(reason) {
        return new Promise((resolve, reject) => {
            reject(reason)
        })
    }
    //all
    static all(promises) {
        const values = [];
        let resolvedCount = 0;
        return new Promise((resolve, reject) => {
            //promisepromise
            promises.forEach((p, index) => {
                Promise.resolve(p).then(
                    //
                    value => {
                        resolvedCount++;
                        values[index] = value;
                        if (resolvedCount == promises.length) {
                            resolve(values);
                        }
                    },
                    reason => {
                        //returnpromise
                        reject(reason)
                    }
                )
            })
        })
    }
    //race
    static race(promise) {
        //pomise
        return new Promise((resolve, reject) => {
            Promise.resolve(p).then(
                //return
                value => {
                    resolve(value);
                },
                //return
                reason => {
                    reject(reason)
                }
            )
        })
    }

    // promiseresolveDelayrejectDelay
    /* promiseresolveDelay
    */
    static resolveDelay(value, time) {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                if (value instanceof Promise) {
                    value.then(resolve, reject)
                } else {
                    resolve(value);
                }
            }, time);
        })
    }
    /*
    promiserejectDelay
    */
    static rejectDelay(reason, time) {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
                reject(reason)
            }, time);
        })
    }
}

export default  myPromise