<template>
    <div>
        <dzs-header title="" v-if="!newIndex"></dzs-header>
        <div class="page">
            <h2></h2>
            <div class="item">
                <dzs-form-header :options="formOptions">
                    <template #mySlot>
                        <el-button>button</el-button>
                    </template>
                </dzs-form-header>
                <dzs-code title="" :value="code1"></dzs-code>
                <dzs-code title="" :value="code2"></dzs-code>
            </div>
            <h2></h2>
            <div class="item">
                <a target="_blank" href="https://github.com/zsduan/zsduan-summary/blob/master/src/components/dzs-form-header/index.vue"></a>
            </div>
        </div>
        <el-backtop :visibility-height="20" v-if="!newIndex"></el-backtop>
    </div>
</template>
<script>
import dzsFormHeader from "../../components/dzs-form-header/index.vue";
import {code1 , code2} from "./config"
export default {
    props:{
        newIndex : {
            type : Boolean,
            default:()=>{
                return false
            }
        }
    },
    components:{
        dzsFormHeader
    },
    computed:{
        formOptions(){
            return {
                formProps : {
                    "label-width" : "100px",
                },
                formItem : [
                    {
                        label: "", 
                        key: "input", 
                        type : "input",
                        rules:[
                            { required: true, message: '', trigger: 'blur' },
                        ],
                        props:{
                            placeholder : "",
                            tips : ""
                        },
                        defaultValue : "",
                        span : 6
                    },
                    {
                        label: "", 
                        key: "select", 
                        type : "select",
                        rules:[
                            { required: true, message: '', trigger: 'change' },
                        ],
                        props:{
                            placeholder : "",
                            clearable  : true
                        },
                        children:[
                            {
                                label : "1",
                                value : 1
                            },
                            {
                                label : "2",
                                value : 2
                            },
                        ],
                        span : 6
                    },
                    {
                        label : "",
                        key : "date",
                        type : "date",
                        span : 8
                    },
                    {
                        label : "",
                        key : "switch",
                        type : "switch",
                        span : 6
                    },
                    {
                        label: "", 
                        key: "checkbox", 
                        type : "checkbox",
                        rules:[],
                        props:{},
                        children:[
                            {
                                label : "1",
                                value : 1
                            },
                            {
                                label : "2",
                                value : 2
                            },
                        ],
                    },
                    {
                        label: "", 
                        key: "radio", 
                        type : "radio",
                        rules:[],
                        props:{},
                        children:[
                            {
                                label : "1",
                                value : 1
                            },
                            {
                                label : "2",
                                value : 2
                            },
                        ],
                        defaultValue : 1
                    },
                    {
                        label: "", 
                        key: "mySlot", 
                        type : "input",
                        isSlot : true , 
                        span : 6
                    },
                ]
            }
        }
    },
    data(){
        return {
            code1 : code1,
            code2 : code2
        }
    }
}
</script>
<style lang="less" scoped>

</style>